/* eslint-disable */
import React, { Component } from 'react';
import isMobile from 'is-mobile';
import Chance from 'chance';
import Press from 'pressure';

import './App.css';
import sara from './sara.mp3';
import jamesblunt from './jamesblunt.mp3';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			size: 20,
			volume: 0,
		};
	}


	componentDidMount() {
		const track = new Chance().pickone([sara, jamesblunt]);
		const that = this;

		let sm = soundManager, // soundManager instance
			trackID = 'uuid-1',
			url = track,
			audio = url,
			mbpPlayer = null;

		function thePlayer() {
			const currentSound = soundManager.createSound({
				id: trackID,
				url: audio,
			});
			sm.play(trackID);
			PressHeartFunc();
		};

		function pressHeart() {
			if (isMobile()) {
				document.addEventListener('touchstart', function() {
					mbpPlayer = new thePlayer();
				});
			} else {
				document.addEventListener('click', function() {
					mbpPlayer = new thePlayer();
				});
			}
		}
		function PressHeartFunc() {
			const heart = document.querySelector('.redheart');
			const heartStyle = heart.style;

			Press.set('body', {
				change(force) {
					heartStyle.width = Press.map(force, 0, 1, 0, that.state.size) + 'em';
					heartStyle.height = Press.map(force, 0, 1, 0, that.state.size) + 'em';
					heartStyle.marginTop = '-' + Press.map(force, 0, 1, 0, that.state.size)/2 + 'em';
					heartStyle.marginLeft = '-' + Press.map(force, 0, 1, 0, that.state.size)/2 + 'em';
					heartStyle.opacity = Press.map(force, 0, 1, 0, 1);


					const volume = Press.map(force, 0, 1, 0, 100);
					sm.getSoundById(trackID).setVolume(volume);
					// console.log(sound);
					// sm.setVolume(volume);
				},
				// startDeepPress() {
				// 	// player.play();
				// },
				// endDeepPress() {
				// 	heartStyle.backgroundColor = '#d9534f';
				// },
				end() {
					heartStyle.width = '0';
					heartStyle.height = '0';
					heartStyle.marginTop = '0';
					heartStyle.marginLeft = '0';
					heartStyle.opacity = '0';
					// player.pause();
					sm.getSoundById(trackID).pause();
				}
			}, { preventSelect: false });
		}

		soundManager.setup({
			url: '/swf/soundmanager2.swf',
			onready: pressHeart,
		});



		const randomBetween = (min, max) => ~~(Math.random() * (max - min + 1)) + min;

		const canvas = document.createElement('canvas');
		const ctx = canvas.getContext('2d');
		const PI2 = Math.PI * 2;
		// const colors = ['#ff4552', '#f75f50', '#ff457b', '#e8944c', '#45ffff'];

		// all set in `setStage`
		let canvasWidth;
		let canvasHeight;
		// let canvasMidX;
		// let canvasMidY;

		let particles = [];

		// let rafId = null;
		// let timeoutId = null;
		let automate = true;
		let tick = 0;
		let frame = 0;

		// noise.seed(Math.random());

		class Particle {
			constructor(options) {
				const defaults = { x: 0, y: 0, radius: 10, direction: 0, velocity: 0, explode: false };

				Object.assign(this, defaults, options);

				this.velX = Math.cos(this.direction) * this.velocity;
				this.velY = Math.sin(this.direction) * this.velocity;

				this.friction = 0.9;
				this.decay = randomBetween(90, 91) * 0.01;
				this.gravity = this.radius * 0.05;
			}

			update() {
				this.x += this.velX;
				this.y += this.velY;

				this.velX *= this.friction;
				this.velY *= this.friction;
				this.velocity *= this.friction;

				// uncomment for a gravity like effect
				// this.velY += this.gravity;

				this.radius *= this.decay;
				this.gravity += 0.05;
			}
		}

		const clear = () => {
			ctx.globalCompositeOperation = 'destination-out';
			ctx.fillStyle = 'hsla(0, 0%, 0%, 1)';
			ctx.fillRect(0, 0, ctx.canvas.width, ctx.canvas.height);
			ctx.globalCompositeOperation = 'lighter';
		}

		const setStage = () => {
			clear();

			canvasWidth = window.innerWidth;
			canvasHeight = window.innerHeight;
			const midX = canvasWidth >> 1;
			const midY = canvasHeight >> 1;

			canvas.width = canvasWidth;
			canvas.height = canvasHeight;
		}

		const createRandomParticles = () => {
			const margin = 20;
			const x = randomBetween(margin, canvasWidth - margin);
			const y = randomBetween(margin, canvasHeight - margin);

			createParticles(x, y);
		}

		const boom = (e) => {
			createParticles(e.clientX, e.clientY);
		}

		const createParticles = (x, y) => {
			let numParticles = 40;

			while (numParticles--) {
				const direction = Math.random() * PI2;
				const velocity = randomBetween(10, 20);
				const radius = 10 + (Math.random() * 20);
				const explode = true;
				const particle = new Particle({
					x,
					y,
					direction,
					velocity,
					radius,
					explode
				});

				particles.push(particle);
			}
		}

		const loop = () => {
			clear();

			if (automate && frame % 75 === 0) {
				createRandomParticles();
			}


			particles.forEach((particle, index) => {
				// determine color
				// const hue = (noise.perlin2(tick, tick) * 360);
				// const fill = `hsl(${hue}, 70%, 50%)`;
				// const fill = colors[randomBetween(0, colors.length - 1)];
				const fill = "#ff4552";

				// draw and update every existing particle
				ctx.beginPath();
				ctx.fillStyle = fill;
				ctx.arc(particle.x, particle.y, particle.radius, 0, PI2);
				ctx.fill();
				ctx.closePath();

				// update particle's properties
				particle.update();

				// check if particle should explode and create new particles
				if (Math.abs(particle.radius) <= 2 && particle.explode) {
					particle.explode = false;
					let children = 10;

					while (children--) {
						particles.push(new Particle({
							x: particle.x,
							y: particle.y,
							radius: particle.radius * 5,
							direction: Math.random() * PI2,
							velocity: particle.velocity * (randomBetween(2, 10)),
							explode: false
						}));
					}
				}

				if (particle.radius <= 0.1 || particle.velocity <= 0.1) {
					particles.splice(particles.indexOf(particle), 1);
				}
			});

			tick += 0.01;

			frame++;
			const rafId = requestAnimationFrame(loop);
		}


		// canvas.addEventListener('mouseout', (e) => {
		// 	automate = true;
		// });
		// canvas.addEventListener('mouseover', (e) => {
		// 	automate = false;
		// });

		// canvas.addEventListener('mousedown', boom);
		window.addEventListener('resize', setStage);

		document.body.appendChild(canvas);

		setStage();
		createRandomParticles();
		loop();
	}
	render() {
		return (
			<div>
				<main>
					<h1>
						You should know We see eye to eye, heart to heart!
						<span id="pressme">❤️Press my Heart!🎶</span>
					</h1>
					{/* <audio id="player" src={track} controls autoPlay>
						Your browser does not support the audio element
					</audio> */}
					{/* {
						this.state.music ?
							<audio id="player" src={track} controls>
								Your browser does not support the audio element
							</audio>
							:
							null
					} */}
					{/* <audio id="player" src={this.state.music ? "http://192.168.1.253:8080/music.mp3" : null} autoPlay /> */}
					{/* <audio id="player" src="http://192.168.1.253:8080/music.mp3" autoPlay /> */}
				</main>
				<i className="redheart"></i>
				<i id="overlay"></i>
			</div>
		);
	}
}

export default App;



